import { Component, Injector, OnInit } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { GenMaintService } from 'src/app/api/controllers/GenMaint';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-op-maintenance-setup-subcategory',
  templateUrl: './op-maintenance-setup-subcategory.component.html',
  styleUrls: ['./op-maintenance-setup-subcategory.component.scss']
})
export class OpMaintenanceSetupSubcategoryComponent implements OnInit {
	modalService: any;
	row: any = {};
	isAssetLoaded: boolean;
	unitFieldType: string = "unitno";
	unitOrder: boolean = false;
	subItemStartIndex: any = 0;
	subItemEndIndex: any;
	subTotalItems: any;
	subItemLimit: any = 10;
	categoriesData = [];
	subCategoriesData: any = [];
	tempSubCategoriesData: any = [];
	subCatgory: any = {};
	opSubIndex: number;
	deleteSubCatgory: boolean;
	currentData:any = {};
	searchSubCategory:string;

	constructor(
		private sessionService: SessionService,
		private injector: Injector,
		private sharedService: SharedService,
		private genMaintService: GenMaintService,
	) {
		this.modalService = this.injector.get(ModalService);
	}

	getIndexParamsSubcategory(event) {
		this.subItemStartIndex = event.itemStartIndex;
		this.subItemEndIndex = event.itemEndIndex;
		this.subItemLimit = event.itemLimit;
	}


	sortUnitData(type) {
		this.unitFieldType = type;
		this.unitOrder = !this.unitOrder;
	}


	getFieldOrderBy(type) {
		if (this.unitFieldType == type) {
			return this.unitOrder ? 'desc' : 'asc';
		}
		else return '';
	}

	isMobileView() {
		return window.innerWidth <= 767 ? 'table-responsive' : '';
	}

	deleteSubRow(index: number, data) {
		this.deleteSubCatgory = true;
		this.modalService.showConfirmModal(data.genMaintenanceSubCategoryId);
	}

	getAllCategory() {
		this.isAssetLoaded = true;
		this.genMaintService.getAllGenMaintenanceCategory().subscribe((res: any) => {
			if (res.length > 0) {
				this.categoriesData = res;
			}
		});
	}

	getAllSubCategory() {
		this.isAssetLoaded = true;
		this.genMaintService.getAllGenMaintenanceSubCategory().subscribe((res: any) => {
			if (res) {
				this.isAssetLoaded = false;
				this.subCategoriesData = res ? res : [];
				this.tempSubCategoriesData = res ? res : [];
				this.subTotalItems = this.subCategoriesData.length;
				if (this.subTotalItems > this.subItemLimit) {
					this.subItemEndIndex = this.subItemLimit;
				}
				else {
					this.subItemEndIndex = this.subTotalItems;
				}
			}
		});
	}

	addSubCategory(data) {
		let reqObj: any = {};
		reqObj = {
			"genMaintenanceSubCategoryId": 0,
			"genMaintenanceCategoryId": data.genMaintenanceCategoryId,
			"genMaintenanceSubCategoryName": data.genMaintenanceSubCategoryName,
			"description": data.description,
			"frequency": data.frequency,
			"isActive": true,
			"insertedBy": parseInt(this.sessionService.userId),
			"insertedOn": new Date().toISOString,
			"updatedBy": 0,
			"updatedOn": new Date().toISOString,
		}
		let params: any = {};
		params.model = reqObj;
		this.genMaintService.addGenMaintenanceSubCategory(params).subscribe((resp: any) => {
			if(!isNaN(resp.message)) {
				this.sharedService.setAlertMessage("Sub Category added successfully");
				this.isAssetLoaded = false;
				this.subCatgory = {};
				this.getAllSubCategory();
			} else if (resp.message)
				this.sharedService.setAlertMessage(resp.message);
			else 
				this.sharedService.setAlertMessage(resp.errorMessage);
		});
	}

	updateSubCategory(data) {
		let reqObj: any = {};
		reqObj = {
			"genMaintenanceSubCategoryId": data.genMaintenanceSubCategoryId,
			"genMaintenanceCategoryId": data.genMaintenanceCategoryId,
			"genMaintenanceSubCategoryName": data.genMaintenanceSubCategoryName,
			"description": data.description,
			"frequency": data.frequency,
			"isActive": true,
			"insertedBy": parseInt(this.sessionService.userId),
			"insertedOn": new Date().toISOString,
			"updatedBy": parseInt(this.sessionService.userId),
			"updatedOn": new Date().toISOString,
		}
		let params: any = {};
		params.model = reqObj;
		this.genMaintService.updateGenMaintenanceSubCategory(params).subscribe((resp: any) => {
			if (resp.message) {
				this.isAssetLoaded = false;
				this.opSubIndex = -1;
				this.getAllSubCategory();
				this.sharedService.setAlertMessage(resp.message);
			} else 
				this.sharedService.setAlertMessage(resp.errorMessage);
		});
	}

	setCurrentData(data) {
		this.currentData = {...data};
	}

	cancelCategory(index,type) {
		this.subCategoriesData.splice(index,1,this.currentData);
	}

	ngOnInit(): void {
		this.getAllCategory();
		this.getAllSubCategory();
		this.sharedService.unitlistdeleteindexcast.subscribe(id => {
			if (id != null) {
				let param: any = {};
				if (this.deleteSubCatgory == true) {
					param = {
						genMaintenanceSubCategoryId: id,
						deleteBy: parseInt(this.sessionService.userId)
					}
					this.genMaintService.deleteGenMaintenanceSubCategory(param).subscribe((res: any) => {
						setTimeout(() => {
							this.sharedService.setAlertMessage(" Sub Category deleted successfully");
							this.sharedService.setUnitListDeleteIndex(null);
							this.getAllSubCategory();
							this.deleteSubCatgory = false;
						}, 500)
					}, error => {
						console.log(error);
					});
				}
			}
		});
	}

}
