import { Component, Injector, OnInit, ViewChild, HostListener, ViewEncapsulation } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { GenMaintService } from 'src/app/api/controllers/GenMaint';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { ENTER, COMMA } from '@angular/cdk/keycodes';
import { MatChipInputEvent } from '@angular/material/chips';
import { MatDialog } from '@angular/material/dialog';
import { DynamicCheckListFormComponent } from '../dynamic-check-list-form/dynamic-check-list-form.component';

import * as moment from 'moment';
import _moment from "moment-timezone";
import { MatDrawer } from '@angular/material/sidenav';
import { ConfirmDialogModel, CommonConfirmModalComponent } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';

@Component({
  selector: 'app-op-maintenance-setup-title',
  templateUrl: './op-maintenance-setup-title.component.html',
  styleUrls: ['./op-maintenance-setup-title.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class OpMaintenanceSetupTitleComponent implements OnInit {
	@ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
	@ViewChild('titleGrid', { static: false }) titleGrid: jqxGridComponent;
	titleHeader: any;
	titleList:any;
	titleFilter:string;
	isDataLoaded:boolean = true;
	title:any={
		customForm:[]
	};
	isCreateTitle:boolean = true;   //Hide and Show Create Title
	isShowCreateTitle:boolean=false; //Hide and Show Create Title
	fieldType:string = '';
	categoriesData = [];
	subCategoriesData = [];
	subCategoriesDropDown= [];
	modalService: any;
	totalItems:number = 0;
	index:number = -1;
	readonly separatorKeysCodes: number[] = [ENTER, COMMA];  //Mat Chip
	radioOption = [];  //radioButton option value
  	constructor(private sessionService: SessionService,
		private injector: Injector,
		private sharedService: SharedService,
		private genMaintService: GenMaintService,
		public dialog: MatDialog) {
			
      this.modalService = this.injector.get(ModalService);
	}
	
	showFields() {
		this.isShowCreateTitle=true;
		this.isCreateTitle=true;
		this.title = {
			categoryId:'',
			subCategoryId:'',
			customForm:[]
		};
	}

	goBack() {
		this.matDrawer.close();
		if(this.index != -1) {
			this.customForm();
		}
	}

	@HostListener('window:onTitleDelete', ['$event.detail'])
	onTitleDelete(detail) {
	  let dataRecord = this.titleGrid.getrowdata(detail.rowId)
	  this.modalService.showConfirmModal(dataRecord.id);
	}

	@HostListener('window:onEditOPTitle', ['$event.detail'])
	onEditOPTitle(detail) {
	  let dataRecord = this.titleGrid.getrowdata(detail.rowId);
	  this.title = dataRecord;
	  this.isCreateTitle = false;
	  this.isShowCreateTitle = true;
	  this.dropDownListOfSubCategory();
	  window.scroll({
        top: 0,
        behavior: 'smooth'
	  });
	  this.fieldType = '';
	  this.title.customForm = JSON.parse(dataRecord.customfields);
	}

	@HostListener('window:onCheckListForm', ['$event.detail'])
	onCheckListForm(detail) {
	  let dataRecord = this.titleGrid.getrowdata(detail.rowId)
      const dialogRef = this.dialog.open(DynamicCheckListFormComponent, {
        width: '600px',
        height:'auto',
        data:dataRecord
      });
	}

	dropDownListOfSubCategory() {
		this.subCategoriesDropDown = this.subCategoriesData.filter(data=>data.genMaintenanceCategoryId==this.title.categoryId)
	}

	searchTitle() {
		if(this.titleFilter != ""){
			let filtergroup = new jqx.filter();
			let filter_or_operator = 1;
			let filtervalue = this.titleFilter;
			let filtercondition = 'contains';
			let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
			filtergroup.operator = 'or';
			filtergroup.addfilter(filter_or_operator, filterData);
			this.titleGrid.showfiltercolumnbackground(false);
			this.titleHeader.forEach(item => {
				if(item.datafield != 'Actions') {
					this.titleGrid.addfilter(item.datafield, filtergroup, true);
				}
			})
			this.titleGrid.applyfilters();
		}
		else {
		 	 this.titleGrid.clearfilters();
		}
	}

	getPrintParams(event) {  //Download Sheet
		this.titleGrid.exportdata(event, 'Title');
	}

	addRadioButtonOption(event: MatChipInputEvent): void {
		const input = event.input;
		const value = event.value;
		// Add option
		if ((value || '').trim()) {
			this.radioOption.push({name: value.trim()});
		}
		// Reset the input value
		if (input) {
			input.value = '';
		}
	}
	
	removeRadioButtonOption(option): void {
		const index = this.radioOption.indexOf(option);
		if (index >= 0) {
			this.radioOption.splice(index, 1);
		}
	}

	customForm() {
		if(this.title.customField) {
			let entity:any = {
				question: this.title.customField,
				answer: this.fieldType == 'check-box' ? false : '',
				type: this.fieldType 
			}
			if(this.fieldType == 'radio') {
				entity.option = [...this.radioOption]
			}
			if(this.index >= 0) {
				this.title.customForm.splice(this.index,1,entity);
			} else {
				this.title.customForm.push(entity);
			}
			this.radioOption = [];
			this.title.customField = "";
			this.fieldType = '';
			this.index = -1;
		}
	}

	editCustomForm(data,index) {
		this.fieldType = data.type;
		this.title.customField = data.question;
		this.index = index;
		if(data.type == 'radio') {
			this.radioOption = data.option
		}
		this.title.customForm.splice(this.index,1);
	}

	deleteCustomForm(index) {
		const message = `Are you sure you want to delete ?`;
		const dialogData = new ConfirmDialogModel("Confirm Action", message);
		const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
		  panelClass: 'material-dialog-medium',
		  disableClose: true,
		  data: dialogData
		});
		dialogRef.afterClosed().subscribe(dialogResult => {
			if (dialogResult) 
				this.title.customForm.splice(index,1);
		})
  	}

	createGenMaintenance(data) {
		let createTitle = {
			model : {
				"id": 0,
				"name": data.name,
				"apartmentId": this.sessionService.apartmentId,
				"categoryId": data.categoryId,
				"location": null,
				"imageId": null,
				"costCenterid": null,
				"costCenterName":null,
				"maintOwnerId": null,
				"maintFrequencyInDays": 0,
				"maintReminderInDays": 0,
				"blockId": null,
				"budgetAmount": 0,
				"comments": null,
				"isActive": true,
				"insertedBy": parseInt(this.sessionService.userId),
				"insertedOn":  _moment().toISOString(),
				"updatedBy": null,
				"updatedOn": null,
				"subCategoryId": data.subCategoryId,
				"serialNo": 0,
  				"customfields": JSON.stringify(data.customForm)
			},
			file : null
		}
		this.genMaintService.addGenMaintenance(createTitle).subscribe((resp: any) => {
			if(!isNaN(resp.message)) {
				this.sharedService.setAlertMessage("Created successfully");
				this.getTitleList();
				this.isShowCreateTitle = !this.isShowCreateTitle;
			} else if (resp.message) {
				this.sharedService.setAlertMessage(resp.message);
				this.isShowCreateTitle = !this.isShowCreateTitle;
			}
			else 
				this.sharedService.setAlertMessage(resp.errorMessage);
		})
  	}
	  
	updateGenMaintenance () {
		let updateTitle = {
			model : { ... this.title,"customfields": JSON.stringify(this.title.customForm)},
			file : null
		}
		this.genMaintService.updateGenMaintenance(updateTitle).subscribe((resp: any) => {
			if(resp.message) {
				this.getTitleList();
				this.title = {};
				this.sharedService.setAlertMessage(resp.message);
				this.isShowCreateTitle = !this.isShowCreateTitle;
			}
			else 
				this.sharedService.setAlertMessage(resp.errorMessage);
		})
	}
  
  	getTitleList() {
		this.isDataLoaded = true;
		let queryParamBase = {
			apartmentId: this.sessionService.apartmentId
		};
		this.genMaintService.getAllGenMaintByApartmentId(queryParamBase).subscribe((resp: any) => {
			if(resp.length > 0) {
				this.totalItems = resp.length;
				this.titleList = resp;
				let arrangeSlotRawData = {
					localdata: resp.reverse(),
					datatype: "array"
				}
				this.titleList = new jqx.dataAdapter(arrangeSlotRawData);
			}
			this.isDataLoaded = false;
		})
	}

  	ngOnInit(): void {
		this.getTitleList();
		this.genMaintService.getAllGenMaintenanceCategory().subscribe((res: any) => {
			if (res.length > 0) {
				this.categoriesData = res;
			}
		});
		this.genMaintService.getAllGenMaintenanceSubCategory().subscribe((res: any) => {
			if (res.length > 0) {
				this.subCategoriesData = res;
			}
		});
		var cellsrenderer = (row, column, value) => {
			return '<div class="jqx-custom-inner-cell">' + value + '</div>';
		}
		var columnrenderer = (value) => {
			return '<div style="padding: 14px">' + value + '</div>';
		}
		this.titleHeader = [
		{
			text: 'Name',
			datafield: 'name',
			cellsrenderer: cellsrenderer,
			renderer: columnrenderer
		},{
			text: 'Category',
			datafield: 'categoryName',
			cellsrenderer: cellsrenderer,
			width : 280,
			renderer: columnrenderer
		},{
			text: 'SubCategory',
			datafield: 'subCategoryName',
			width : 280,
			cellsrenderer: cellsrenderer,
			renderer: columnrenderer
		},{
			text: 'Actions',
			cellsalign: 'center',
			align: 'center',
			cellsrenderer: (row): string => {
				return '<div class="simple-actions">'
				+'<a href="javascript:void(0)" class="mr-2" onClick="editOPTitle(' + row + ')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a>'
				+'<a href="javascript:void(0)" class="mr-2" onClick="showConfirmDelete(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a>'
				+'<a href="javascript:void(0)" onClick="checkListForm('+ row +')"><i class="fa fa-check-circle icon delete" aria-hidden="true"></i></a>';
				+'</div>'
			},
			width: 120,
			renderer: columnrenderer
		}];
		//delete Title
		this.sharedService.unitlistdeleteindexcast.subscribe( item => {
			if(item != null){
				var params = {
					id: item,
					deleteBy: parseInt(this.sessionService.userId)
				}
				this.genMaintService.deleteGenMaintenance(params).subscribe((res:any) => {
					this.sharedService.setUnitListDeleteIndex(null);
					this.getTitleList();
				});
			}
		});
  	}
}

function editOPTitle(row){
	var event = new CustomEvent('onEditOPTitle', {
	  detail: {
		rowId: row
	  }
	})
	window.dispatchEvent(event);
  }
(window as any).editOPTitle = editOPTitle;

function showConfirmDelete(row){
	var event = new CustomEvent('onTitleDelete', {
	  detail: {
		  rowId: row
	  }
	})
	window.dispatchEvent(event);
}
(window as any).showConfirmDelete = showConfirmDelete;

function checkListForm(row){
	var event = new CustomEvent('onCheckListForm', {
	  detail: {
		  rowId: row
	  }
	})
	window.dispatchEvent(event);
}
(window as any).checkListForm = checkListForm;

