import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-dynamic-check-list-form',
  templateUrl: './dynamic-check-list-form.component.html',
  styleUrls: ['./dynamic-check-list-form.component.scss']
})
export class DynamicCheckListFormComponent implements OnInit {
  dynamicForm = [];
  constructor(public dialogRef: MatDialogRef<DynamicCheckListFormComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      let checklist = JSON.parse(this.data.customfields);
      this.dynamicForm = checklist
    }

  ngOnInit(): void {
  }

}
