import { Component, OnInit ,ViewChild} from '@angular/core';
import { GenMaintService } from 'src/app/api/controllers/GenMaint';
import { SessionService } from 'src/app/core/session/session.service';
import { StaffService } from 'src/app/api/controllers/Staff';
import { SharedService } from 'src/app/shared/services/shared.service';
import { Router, ActivatedRoute } from '@angular/router';
import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-maintain-operational-maintanance',
  templateUrl: './maintain-operational-maintanance.component.html',
  styleUrls: ['./maintain-operational-maintanance.component.scss']
})
export class MaintainOperationalMaintananceComponent implements OnInit {
  listOfFiles: any;
  isMaintainedbyStaff:any =true;
  fileList: any;
  selectedFile: any;
  isFileAdded: boolean;
  asset:any ={};
  ckeditor:any ;
  message:any={};
  subCategoriesData:any =[];
  categoriesData:any =[];
  subCategoriesDropDown:any = [];
  maintain:any ={};
  staffList: any = {
    "dropdownList":[],
    "selectedItems":[]
  };
  currentDate:any =new Date();
  opMaintenancePlanId: string;
  isEdit: boolean = false;
  staffsdropdownSettings:any ={};
  titleList = [];
  titleDropDown = [];
  dynamicForm = [];
  quillModules:any;
 
  constructor(
    private genMaintService:GenMaintService,
    private sessionService:SessionService,
    private staffService:StaffService,
    private sharedService:SharedService,
    private router:Router,
    private route: ActivatedRoute,
  ) {
    this.quillModules = {
			toolbar: [
			  [{ font: [] }],
			  [{ size: ['small', false, 'large', 'huge'] }],
			  ['bold', 'italic', 'underline'],
			  [{ color: [] }, { background: [] }],
			  [{ align: [] }],
			  [{ list: 'ordered' }, { list: 'bullet' }],
			  [{ indent: '-1' }, { indent: '+1' }],
			  ['blockquote', 'code-block', 'strike'],
			  ['clean']
			]
		};
  }

  ngOnInit(): void {
    this.getAllCategory();
    this.getAllSubCategory();
    this.getAllStaff();
    this.getTitleList();
    this.staffsdropdownSettings={
      singleSelection: true,
      primaryKey: 'userId',
      labelKey: 'customLabel',
      enableSearchFilter: true,
      enableFilterSelectAll: false,
      autoPosition: false,
      maxHeight:130
    };
    this.maintain.categoryId ='';
    this.maintain.subCategoryId ='';
    this.maintain.isAssociationStaff =true;
    this.maintain.performedbyAssociationStaffId ='';
    this.route.paramMap.subscribe(params => {
    if (params) {
      this.opMaintenancePlanId = params.get('id');

    }
    });
    if (this.opMaintenancePlanId) {
      let params: any = {};
      this.isEdit = true;
      params.id = this.opMaintenancePlanId;
      this.genMaintService.getGenMaintenancePlanById(params).subscribe((res: any) => {
        this.maintain = res[0] ? res[0] : {};
        this.dropDownListOfSubCategory();
        this.dropDownOfTitle();
        let customField = JSON.parse(this.maintain.customFields);
        if(customField && customField.length > 0 )
        this.dynamicForm = customField;
      });
    }

  }

  uploadFile(event){
    this.isFileAdded = true;

    for (var i = 0; i <= event.target.files.length - 1; i++) {
      var selectedFile = event.target.files[i];
      this.fileList.push(selectedFile);
      this.listOfFiles.push({'name':selectedFile.name, 'size':selectedFile.size})
    }

  }

  deleteFile(index){
    // Delete the item from fileNames list
    this.listOfFiles.splice(index, 1);
    // delete file from FileList
    this.fileList.splice(index, 1);

    this.selectedFile = null;

    if(this.fileList.length == 0 && this.listOfFiles.length == 0){
      this.isFileAdded = false;
    }

  }
  onContentChanged(event){
    
  }
  getAllCategory() {
		this.genMaintService.getAllGenMaintenanceCategory().subscribe((res: any) => {
			if (res) {
				this.categoriesData = res ? res : [];
			
			}
		});
	}
  getAllSubCategory() {
		this.genMaintService.getAllGenMaintenanceSubCategory().subscribe((res: any) => {
			if (res) {
				this.subCategoriesData = res ? res : [];
			
			}
		});
	}

	getAllStaff() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.sessionService.apartmentId
		};
		this.staffService.getAllStaffsByApartmentId(queryParamBase).subscribe((res: any) => {
      if(res.length > 0) {
        this.staffList.dropdownList = res.filter((ele)=>{
          ele.customLabel = `${ele.firstName} ${ele.lastName} ${ele.staffCategory_Label}`
          return ele.isActive
        });
        if(this.isEdit) {
          this.staffList.selectedItems = this.staffList.dropdownList.filter(data=>data.staffId == parseInt(this.maintain.performedbyAssociationStaffId));
        }
      }
		});
	}
  addMaintanance(){
    let reqObj:any = {};
    reqObj= {
      "id": 0,
      "apartmentId":  this.sessionService.apartmentId,
      "genMaintenanceId": this.maintain.genMaintenanceId,
      "notes": this.maintain.notes,
      "maintenanceStatusId": null,
      "firstMaintDate": null,
      "actualMaintDate": this.maintain.actualMaintDate,
      "lastMaintenanceDate": this.maintain.lastMaintenanceDate,
      "nextPlannedMaintenance": this.maintain.nextPlannedMaintenance,
      "maintenanceTypeId": null,
      "maintenanceByCompany": this.maintain.maintenanceByCompany,
      "maintenanceByPerson": null,
      "maintenanceContactNo": null,
      "maintLaborCost":Number(this.maintain.maintLaborCost),
      "maintPartsCost": Number(this.maintain.maintPartsCost),
      "maintVat":  Number(this.maintain.maintVat),
      "maintWithTax": Number(this.maintain.maintWithTax),
      "maintenanceTotalBilledAmt": Number(this.maintain.totalBilledAmount),
      "comments": this.maintain.comments,
      "description": null,
      "imageId": null,
      "isActive": true,
      "insertedBy": parseInt(this.sessionService.userId),
      "insertedOn": _moment().toISOString(),
      "updatedBy": this.sessionService.userId,
      "updatedOn": _moment().toISOString(),
      "performedAtLocationId":  null,
      "maintenanceLocation": null,
      "serialNo": null,
      "customFields": JSON.stringify(this.dynamicForm),
      // "maintCategory": "Mc1",
      "maintCategory":null,
      "maintTitle": this.maintain.maintTitle,
      "plannedMaintenanceDate": this.maintain.plannedMaintenanceDate,
      "totalBilledAmount": Number(this.maintain.totalBilledAmount),
      "alertBeforeDays": Number(this.maintain.alertBeforeDays),
      "categoryId":   Number(this.maintain.categoryId),
      "subCategoryId": Number(this.maintain.subCategoryId),
      "recordNo": Number(this.maintain.recordNo),
      "isAssociationStaff": this.maintain.isAssociationStaff,
      // "performedbyAssociationStaffId":  this.staffList.selectedItems.length > 0 ? this.staffList.selectedItems[0].staffId : null
       "performedbyAssociationStaffId":  this.maintain.performedbyAssociationStaffId  ? this.maintain.performedbyAssociationStaffId : null
      }
    let params: any = {};
		params.genMaintenancePlan = reqObj;
		this.genMaintService.addGenMaintenancePlan(params).subscribe((res: any) => {
			if (res.message) {
				this.sharedService.openSnackBar("Operational Maintanance added successfully",'success');
        // this.isAssetLoaded = false;
        this.router.navigate(['ams/operational-maintenance/operational-maintenance-list']);
			}  else {
        this.sharedService.openSnackBar(res.errorMessage,'error');
      }
		},(error)=>{
      this.sharedService.openSnackBar('Server Error','error');
    });
  }


  updateMaintanance(){

    let reqObj:any = {};

    reqObj= {
      "id": this.maintain.id,
      "apartmentId":  this.sessionService.apartmentId,
      "genMaintenanceId": this.maintain.genMaintenanceId,
      "notes": this.maintain.notes,
      "maintenanceStatusId": null,
      "firstMaintDate": this.maintain.lastMaintenanceDate,
      "actualMaintDate": this.maintain.actualMaintDate,
      "lastMaintenanceDate": this.maintain.lastMaintenanceDate,
      "nextPlannedMaintenance": this.maintain.nextPlannedMaintenance,
      "maintenanceTypeId": null,
      "maintenanceByCompany": this.maintain.maintenanceByCompany,
      "maintenanceByPerson": null,
      "maintenanceContactNo": null,
      "maintLaborCost":Number(this.maintain.maintLaborCost),
      "maintPartsCost": Number(this.maintain.maintPartsCost),
      "maintVat":  Number(this.maintain.maintVat),
      "maintWithTax": Number(this.maintain.maintWithTax),
      "maintenanceTotalBilledAmt": Number(this.maintain.totalBilledAmount),
      "comments": this.maintain.comments,
      "description": null,
      "imageId": null,
      "isActive": true,
      "insertedBy": parseInt(this.sessionService.userId),
      "insertedOn":  _moment().toISOString(),
      "updatedBy": parseInt(this.sessionService.userId),
      "updatedOn":  _moment().toISOString(),
      "performedAtLocationId":  null,
      "maintenanceLocation": null,
      "serialNo": this.maintain.serialNo,
      // "customFields": "cf1",
      "customFields": JSON.stringify(this.dynamicForm),
      // "maintCategory": "Mc1",
      "maintCategory":null,
      "maintTitle": this.maintain.maintTitle,
      "plannedMaintenanceDate": this.maintain.plannedMaintenanceDate,
      "totalBilledAmount": Number(this.maintain.totalBilledAmount),
      "alertBeforeDays": Number(this.maintain.alertBeforeDays),
      "categoryId":   Number(this.maintain.categoryId),
      "subCategoryId": Number(this.maintain.subCategoryId),
      "recordNo": Number(this.maintain.recordNo),
      "isAssociationStaff": this.maintain.isAssociationStaff,
      // "performedbyAssociationStaffId": this.staffList.selectedItems.length > 0 ? this.staffList.selectedItems[0].staffId : null
      "performedbyAssociationStaffId":  this.maintain.performedbyAssociationStaffId  ? 
                                    this.maintain.performedbyAssociationStaffId : null      
    }
    let params: any = {};
		params.genMaintenancePlan = reqObj;
		this.genMaintService.updateGenMaintenancePlan(params).subscribe((res: any) => {
	  	if ( res && res.message) {
				this.sharedService.openSnackBar("Operational Maintanance updated successfully",'success');
        // this.isAssetLoaded = false;
        this.router.navigate(['ams/operational-maintenance/operational-maintenance-list']);
			} else {
        this.sharedService.openSnackBar(res.errorMessage,'error');
      }
		},(error)=>{
      this.sharedService.openSnackBar('Server Error','error');
    });
  }

  dropDownListOfSubCategory() {
    this.subCategoriesDropDown = this.subCategoriesData.filter(data=>data.genMaintenanceCategoryId==this.maintain.categoryId)
  }

  totalAmount() {
    let labour, parts, mainVat, maintWithTax;
    labour = this.maintain.maintLaborCost ? parseInt(this.maintain.maintLaborCost) : 0;
    parts = this.maintain.maintPartsCost ? parseInt(this.maintain.maintPartsCost) : 0
    mainVat = this.maintain.maintVat ? parseInt(this.maintain.maintVat) : 0
    maintWithTax = this.maintain.maintWithTax ? parseInt(this.maintain.maintWithTax) : 0
    this.maintain.totalBilledAmount =  (labour+parts+mainVat+maintWithTax).toFixed(2);
  }

  selectPlannedMaintenance() {
    let subcatInfo,day;
    if(this.maintain.subCategoryId && (this.maintain.actualMaintDate)) {
      subcatInfo = this.subCategoriesDropDown.filter(ele=>ele.genMaintenanceSubCategoryId == this.maintain.subCategoryId);
      day = subcatInfo[0].frequency ? parseInt(subcatInfo[0].frequency) : null;
      if(day) {
        this.maintain.nextPlannedMaintenance =  moment(this.maintain.actualMaintDate).add(day, 'days');
      }
    }
  }

  getTitleList() {
		let queryParamBase = {
			apartmentId: this.sessionService.apartmentId
		};
		this.genMaintService.getAllGenMaintByApartmentId(queryParamBase).subscribe((resp: any) => {
			if(resp.length > 0) {
				this.titleList = resp;
			}
		})
  }
  
  //get title dropdown for selected category and subcategory 
  dropDownOfTitle() {
    this.titleDropDown = this.titleList.filter((data)=>data.categoryId == this.maintain.categoryId && data.subCategoryId == this.maintain.subCategoryId);
  }

  // get dynamic form for selected title 
  getDynamicForm() {
    let obj =this.titleDropDown.filter((data)=>data.id == this.maintain.genMaintenanceId);
    this.maintain.maintTitle = obj[0].name;
    this.dynamicForm = JSON.parse(obj[0].customfields);
  }

}
