import { Component, Injector, OnInit } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { GenMaintService } from 'src/app/api/controllers/GenMaint';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-op-maintenance-setup-category',
  templateUrl: './op-maintenance-setup-category.component.html',
  styleUrls: ['./op-maintenance-setup-category.component.scss']
})
export class OpMaintenanceSetupCategoryComponent implements OnInit {
  categoriesData: any = [];
  modalService: any;
  row: any = {};
  isAssetLoaded: boolean;
  opIndex: number;
  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;
  unitFieldType: string = "unitno";
  unitOrder: boolean = false;
  tempCategoriesData: any = [];
  currentData:any = {};
  searchCategory:string;

	constructor(
		private sessionService: SessionService,
		private injector: Injector,
		private sharedService: SharedService,
		private genMaintService: GenMaintService,
	) {
		this.modalService = this.injector.get(ModalService);
	}

	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		this.itemLimit = event.itemLimit;
	}

	sortUnitData(type) {
		this.unitFieldType = type;
		this.unitOrder = !this.unitOrder;
	}

	getFieldOrderBy(type) {
		if (this.unitFieldType == type) {
			return this.unitOrder ? 'desc' : 'asc';
		}
		else return '';
	}

	isMobileView() {
		return window.innerWidth <= 767 ? 'table-responsive' : '';
	}


	deleteRow(index: number, data) {
		this.modalService.showConfirmModal(data.genMaintenanceCategoryId);
	}

	getAllCategory() {
		this.isAssetLoaded = true;
		this.genMaintService.getAllGenMaintenanceCategory().subscribe((res: any) => {
			if (res) {
				this.categoriesData = res ? res : [];
				this.tempCategoriesData = this.categoriesData;
				this.totalItems = this.categoriesData.length;
				if (this.totalItems > this.itemLimit) {
					this.ItemEndIndex = this.itemLimit;
				}else {
					this.ItemEndIndex = this.totalItems;
				}
			}
		});
	}

	addCategory(data) {
		let reqObj: any = {};
		reqObj = {
			"genMaintenanceCategoryId": 0,
			"apartmentId": this.sessionService.apartmentId,
			"genMaintenanceCategoryName": data.genMaintenanceCategoryName,
			"description": data.description,
			"isActive": true,
			"insertedBy": parseInt(this.sessionService.userId),
			"insertedOn": new Date().toISOString,
			"updatedBy": 0,
			"updatedOn": new Date().toISOString,
		}

		let params: any = {};
		params.model = reqObj;
		this.genMaintService.addGenMaintenanceCategory(params).subscribe((resp: any) => {
			if(!isNaN(resp.message)) {
				this.sharedService.setAlertMessage("Category added successfully");
				this.isAssetLoaded = false;
				this.row = {};
				this.getAllCategory();
			} else if (resp.message)
				this.sharedService.setAlertMessage(resp.message);
			else 
				this.sharedService.setAlertMessage(resp.errorMessage);
		});
	}

	updateCategory(data) {
		let reqObj: any = {};
		reqObj = {
			"genMaintenanceCategoryId": data.genMaintenanceCategoryId,
			"apartmentId": this.sessionService.apartmentId,
			"genMaintenanceCategoryName": data.genMaintenanceCategoryName,
			"description": data.description,
			"isActive": true,
			"insertedBy": parseInt(this.sessionService.userId),
			"insertedOn": new Date().toISOString,
			"updatedBy": parseInt(this.sessionService.userId),
			"updatedOn": new Date().toISOString,
		}
		let params: any = {};
		params.model = reqObj;
		this.genMaintService.updateGenMaintenanceCategory(params).subscribe((resp: any) => {
			if (resp.message) {
				this.isAssetLoaded = false;
				this.opIndex = -1;
				this.sharedService.setAlertMessage(resp.message);
			} else 
				this.sharedService.setAlertMessage(resp.errorMessage);
		});
	}

	
	setCurrentData(data) {
		this.currentData = {...data};
	}

	cancelCategory(index,type) {
		this.categoriesData.splice(index,1,this.currentData);
	}

	ngOnInit(): void {
		this.getAllCategory();
		this.sharedService.unitlistdeleteindexcast.subscribe(id => {
			if (id != null) {
				let param = {
					genMaintenanceCategoryId: id,
					deleteBy: parseInt(this.sessionService.userId)
				}
				this.genMaintService.deleteGenMaintenanceCategory(param).subscribe((res: any) => {
					setTimeout(() => {
						this.sharedService.setAlertMessage("Category deleted successfully");
						this.sharedService.setUnitListDeleteIndex(null);
						this.getAllCategory();
					}, 500)
				}, error => {
					console.log(error);
				});
			}
		});
	}
	  
}