
import { Component, OnInit, ViewEncapsulation, Injector, ChangeDetectorRef, ViewChild, TemplateRef } from '@angular/core';
// import { LookupService, AssetService } from 'src/app/api/services';
import { SessionService } from 'src/app/core/session/session.service';
import { ActivatedRoute, Router } from '@angular/router';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { GenMaintService } from 'src/app/api/controllers/GenMaint';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CsvService } from 'src/app/shared/services/csv.service';

import _moment from "moment-timezone"
import { Subscription } from 'rxjs';
import { MatDrawer } from '@angular/material/sidenav';
import { ConfirmDialogModel, CommonConfirmModalComponent } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';
import { JsonPipe } from '@angular/common';
@Component({
	selector: 'app-operational-maintanance-list',
	templateUrl: './operational-maintanance-list.component.html',
	styleUrls: ['./operational-maintanance-list.component.scss'],
	encapsulation: ViewEncapsulation.None
})
export class OperationalMaintananceListComponent implements OnInit {
	@ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
	@ViewChild("reasonTemplate", { static: false }) reasonTemplate: TemplateRef<any>;
	modalService: any;
	public search: any = {};
	public maintList: any = [];
	public statusData: any = [];
	ItemStartIndex: any = 0;
	ItemEndIndex: any;
	totalItems: any;
	itemLimit: any = 10;
	isDataLoaded:boolean = false;
	timeZone:any;
	apiSubscribe:Subscription;
	reasonComment:string = '';
	customFieldComment:string = '';
	isSubmitReason:boolean;
	selectedList:any = {};
	dynamicForm:any = [];


	constructor(
		private router: Router,
		private csvService:CsvService,
		private sessionService: SessionService,
		private lookupService: LookupService,
		public dialog: MatDialog,
		public genMaintService: GenMaintService,
		private injector: Injector,
		private sharedService: SharedService,
		private cd:ChangeDetectorRef
	) {
		this.modalService = this.injector.get(ModalService);
	}

	getDateTimeFormat(date) {
		if(date) {
			return _moment(date).format(this.timeZone.time);
		} else {
			return ''
		}
	}

	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		this.itemLimit = event.itemLimit;
	}

	download(){
		let headerArray = ['maintTitle','categoryName','subCategoryName',
		'plannedMaintenanceDate',	'actualMaintDate','maintenanceByCompany',
		'performedbyAssociationStaffName','notes','nextPlannedMaintenance','maintLaborCost',
		'maintPartsCost','maintVat','maintWithTax','maintenanceTotalBilledAmt',
		'comments','maintenanceStatusName']
	   	this.csvService.downloadFile(this.maintList,headerArray, 'opManageMaintenance');
	}


	deleteManitenance(row) {
		this.modalService.showConfirmModal(row.id);
	}

	openDrawer(data) {
		this.matDrawer.open();
		if(data.customFields) {
			this.dynamicForm = this.parseJson(data.customFields);
		}
		this.selectedList = {
			id : data.id,
			insertedBy: data.insertedBy,
			insertedOn: data.insertedOn,
			comments: data.commentsNode
		}
	}

	closeDrawer() {
		this.matDrawer.close();
		this.getAllMaitainList();
	}

	updateCustomField() {
		let params = {
			model : {
				"id": this.selectedList.id,
				"apartmentId": this.sessionService.apartmentId,
				"isActive": true,
				"insertedBy": this.selectedList.insertedBy,
				"insertedOn": this.selectedList.insertedOn,
				"updatedBy": this.sessionService.userId,
				"updatedOn": _moment().toISOString(),
				"customfields": JSON.stringify(this.dynamicForm)
			}
		}
		this.genMaintService.updateGenMaintenancePlanCustomfields(params).subscribe((res:any)=>{
			if(res.message) {
				this.sharedService.openSnackBar(res.message,'success');
			} else {
				this.sharedService.openSnackBar(res.errorMessage,'error');
			}
		},(error)=>{
			this.sharedService.openSnackBar('Server Error','error');
		})
	}

	submitCustomFieldComment() {
		let comment = this.customFieldComment.trim();
		if(comment) {
			let params = {
				model : {
					"genMaintenancePlanId": this.selectedList.id,
					"comments": this.customFieldComment,
					"isActive": true,
					"insertedBy": this.sessionService.userId,
					"insertedOn":  _moment().toISOString(),
					"updatedBy": this.sessionService.userId,
					"updatedOn": _moment().toISOString()
				}
			}
			this.genMaintService.addGenMaintenancePlanComments(params).subscribe((res:any)=>{
				if(res.message) {
					this.sharedService.openSnackBar('Comment Added Successfully','success');
				} else {
					this.sharedService.openSnackBar(res.errorMessage,'error');
				}
			},(error)=>{
				this.sharedService.openSnackBar('Server Error','error');
			})
		}
	}

	submitReasonComment() {
		this.isSubmitReason = true;
		this.dialog.closeAll();
	}
	

	addComments(lookup, main,index): void {
		if(main.maintenanceStatusId != 197) {
			if(main.maintLaborCost && main.maintPartsCost && main.maintenanceTotalBilledAmt) {
				this.cd.detectChanges();
				main.isError = false;
				let status = (lookup.lookupValueName).toLowerCase();
				const message = `Are you sure you want to ${status}?`;
				const dialogData = new ConfirmDialogModel("Confirm Action", message);
				const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
				  panelClass: 'material-dialog-medium',
				  disableClose: true,
				  data: dialogData
				});
				dialogRef.afterClosed().subscribe(result => {
					if (result) {
						this.reasonComment = '';
						this.isSubmitReason = false;
						let reasonRef = this.dialog.open(this.reasonTemplate, {
							panelClass: 'material-dialog-medium'
						})
						reasonRef.afterClosed().subscribe(result => {
							if(this.isSubmitReason) {
								main.maintenanceStatusId = lookup.lookupValueId;
								main.maintainStatusName = lookup.lookupValueName;
								main.comments = this.reasonComment;
								let {customNotes,error,...mainData} = main;
								this.updateMaintanancePlan(mainData);
							} else {
								this.cd.detectChanges();
								main.maintenanceStatusId = 197;
							}
						})
					} else {
						this.cd.detectChanges();
						main.maintenanceStatusId = 197;
					}
				})
			} else {
				this.cd.detectChanges();
				main.maintenanceStatusId = 197;
				main.isError = true;
			}
		} 
	}

	updateMaintanancePlan(data) {
		let params = {
			genMaintenancePlan : {...data,
				"updatedBy": this.sessionService.userId,
				"updatedOn": _moment().toISOString(),
			}
		};
		this.genMaintService.updateGenMaintenancePlan(params).subscribe((res: any) => {
			if (res.message) {
				this.getAllMaitainList();
				this.sharedService.openSnackBar("Operational Maintanance updated successfully",'success');
			} else {
				this.sharedService.openSnackBar(res.errorMessage,'error');
			}
		},(error)=>{
			this.sharedService.openSnackBar('Server Error','error');
		});
	}

	parseJson(data) {
		return JSON.parse(data);
	}

	getAllMaitainList() {
		this.isDataLoaded = true;
		let queryParamBase = {
			apartmentId: this.sessionService.apartmentId
		};
		this.genMaintService.getAllGenMaintenancePlanByApartmentId(queryParamBase).subscribe((res: any) => {
			this.isDataLoaded = false;
			if (res.length > 0) {
				this.maintList = res.reverse();
				this.maintList.forEach(data=>{
					if(!data.maintenanceStatusId) {
						data.maintenanceStatusId = 197;
					}
					if(data.notes) 
					data.customNotes = data.notes.replace(/<[^>]*>/g, '');
					data.isError = false;
				})
				this.totalItems = this.maintList.length;
				if (this.totalItems > this.itemLimit) {
					this.ItemEndIndex = this.itemLimit;
				} else {
					this.ItemEndIndex = this.totalItems;
				}
			} 
		},(error)=>{
			this.isDataLoaded = false;
			this.sharedService.openSnackBar('Server Error','error');
		});
	}
	
	ngOnInit(): void {
		//TimeZone
		this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone );
		//Status List
		let queryParamBase = {
			ApartmentId: this.sessionService.apartmentId,
			LookupTypeId: 190,
		};
		this.lookupService.getLookupValueByLookupTypeId(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.statusData = res;
			}
		});
		this.getAllMaitainList();

		// delete maintenance
		this.apiSubscribe = this.sharedService.unitlistdeleteindexcast.subscribe(id => {
			if (id != null) {
				let param = {
					genMaintenancePlanId: id,
					deleteBy: this.sessionService.userId
				}
				this.genMaintService.deleteGenMaintenancePlan(param).subscribe((res: any) => {
					this.sharedService.setUnitListDeleteIndex(null);
					if(res.message) {
						this.getAllMaitainList();
						this.sharedService.openSnackBar("Maintenance  deleted successfully",'success');
					} else {
						this.sharedService.openSnackBar(res.errorMessage,'error');
					}
				}, error => {
					this.sharedService.setUnitListDeleteIndex(null);
					this.sharedService.openSnackBar('Server Error','error');	
				});
			}
		});
	}

	ngOnDestroy() {
		this.apiSubscribe.unsubscribe();
	}
}
