import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { OperationalMaintananceSetupComponent } from './operational-maintanance-setup/operational-maintanance-setup.component';
import { InitialMaintananceComponent } from './initial-maintanance/initial-maintanance.component';
import { MaintainOperationalMaintananceComponent } from './maintain-operational-maintanance/maintain-operational-maintanance.component';
import { OperationalMaintananceListComponent } from './operational-maintanance-list/operational-maintanance-list.component';
import { OperationalMaintananceHistoryComponent } from './operational-maintanance-history/operational-maintanance-history.component';
import { OpMaintenanceSetupTitleComponent } from './operational-maintanance-setup/op-maintenance-setup-title/op-maintenance-setup-title.component';
import { OpMaintenanceSetupSubcategoryComponent } from './operational-maintanance-setup/op-maintenance-setup-subcategory/op-maintenance-setup-subcategory.component';
import { OpMaintenanceSetupCategoryComponent } from './operational-maintanance-setup/op-maintenance-setup-category/op-maintenance-setup-category.component';




const routes: Routes = [
	{ path: '', redirectTo: 'setup', pathMatch: 'full'},
	{ path: 'setup', component: OperationalMaintananceSetupComponent, canActivate: [AuthGuard],
    children:[
		{ path: '', redirectTo: 'category', pathMatch: 'full'},
		{ path: 'category', component: OpMaintenanceSetupCategoryComponent, canActivate: [AuthGuard]},
		{ path: 'sub-category', component: OpMaintenanceSetupSubcategoryComponent, canActivate: [AuthGuard]},
		{ path: 'title', component: OpMaintenanceSetupTitleComponent, canActivate: [AuthGuard]},

	]},
	{ path: 'title', component: OpMaintenanceSetupTitleComponent, canActivate: [AuthGuard]},
	{ path: 'maintain-operational-maintenance', component: MaintainOperationalMaintananceComponent, canActivate: [AuthGuard]},
	{ path: 'maintain-operational-maintenance/:id', component: MaintainOperationalMaintananceComponent, canActivate: [AuthGuard]},
	{ path: 'operational-maintenance-list', component: OperationalMaintananceListComponent, canActivate: [AuthGuard]},
	{ path: 'operational-maintenance-history', component: OperationalMaintananceHistoryComponent, canActivate: [AuthGuard]},
	{ path: '**', redirectTo: 'setup', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OperationalMaintananceRouting { }
